import { ethers } from "ethers";
import { useEffect, useState } from "react";
import "./PayRent.css";
const PayRent = ({ property, paytogglePop, getOrderData, tokenId }) => {
  const [loading, setLoading] = useState(true); // 加载状态
  const [error, setError] = useState(null); // 错误状态
  const [payLoading, setpayLoading] = useState(false);
  const [paySuccess, setpaySuccess] = useState(false);
  const [newEndtime, setnewEndtime] = useState(null);
  // 获取房产、租赁托管 合约实例
  const rentalProperty = property.rentalProperty;

  const rentalEscrow = property.rentalEscrow;
  const payOneMonth = async () => {
    if (typeof window.ethereum !== "undefined") {
      try {
        setpayLoading(true); // 开始处理时显示弹窗
        // 获取当前连接的账户
        const accounts = await window.ethereum.request({
          method: "eth_accounts",
        });

        if (accounts.length === 0) {
          // 如果没有连接账户，弹出提示
          alert("🔗 Please connect your MetaMask wallet first");
          return;
        }
        console.log("续租处理");

        console.log("房产信息：", rentalEscrow.address);
        // 获取账户信息
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const tenant1 = provider.getSigner();

        let tenantBalance = await provider.getBalance(property.tenant);
        let tenantBalanceInEth = ethers.utils.formatEther(tenantBalance);
        console.log("tenant余额: ", tenantBalanceInEth, "ETH");

        let landlordBalance = await provider.getBalance(property.landlord);
        let landlordBalanceInEth = ethers.utils.formatEther(landlordBalance);
        console.log("landlord地址余额: ", landlordBalanceInEth, "ETH");
        // 开始处理续租
        console.log("property.rentalProperty", property.rentalEscrowAddress);
        const rentPrice1 = ethers.utils.parseEther(
          property.rentPrice.toString()
        );
        console.log("rentPrice1: ", rentPrice1);
        const transaction = await rentalEscrow
          .connect(tenant1)
          .payRent(property.id, {
            value: rentPrice1,
            gasLimit: 5000000,
          });
        const receipt = await transaction.wait();
        console.log("receipt:", receipt);
        const escrowInfo = await rentalEscrow.getRentalEscrowInfo(tokenId);
        setnewEndtime(new Date(escrowInfo.endTime * 1000).toLocaleDateString());
        console.log("escrowInfo-newEndtime:", newEndtime);
        tenantBalance = await provider.getBalance(property.tenant);
        tenantBalanceInEth = ethers.utils.formatEther(tenantBalance);
        console.log("续租后，tenant余额: ", tenantBalanceInEth, "ETH");

        landlordBalance = await provider.getBalance(property.landlord);
        landlordBalanceInEth = ethers.utils.formatEther(landlordBalance);
        console.log("续租后，landlord地址余额: ", landlordBalanceInEth, "ETH");
        console.log("续租成功");
        // 更新页面
        await getOrderData();

        setpayLoading(false); // 完成后隐藏弹窗
        setpaySuccess(true); // 租房成功弹窗
      } catch (error) {
        console.error("Error in tenant handling:", error);
        alert("Sorry, Rental failed, please view console log");
        setError("Error processing rental request");
      } finally {
        setpayLoading(false); // 完成后隐藏弹窗
      }
    } else {
      alert("MetaMask is not installed. Please install it to proceed.");
    }
  };

  return (
    <div className="payRent">
      <div className="pay_loading_overlay">
        <div className="pay_loading_spinner">
          {paySuccess ? (
            <div className="pay_successMessage">
              <div className="pay_successMessage_p">
                <p>
                  <strong> 🎉 Great News! </strong>
                </p>
                <p>
                  You've successfully renewed your home sweet home for another
                  month.
                </p>
                <p>
                  {" "}
                  Your rental end date has been extended to{" "}
                  <strong>{newEndtime}</strong>. See you in March!
                </p>
                <p>Confirm Your Renewal</p>
              </div>
              <div className="loading_successMessage_button">
                <button onClick={paytogglePop}>confirm</button>
              </div>
            </div>
          ) : (
            <div className="confilm_loading_message">
              {" "}
              {payLoading ? (
                <div className="loading_message">
                  Processing, please wait a moment...
                </div>
              ) : (
                <div className="pay_confilmMessage">
                  <div className="pay_confilmMessage_p">
                    <p>🌟 Hello there!</p>{" "}
                    <p>Are you ready to renew your rental for another month?</p>
                  </div>
                  <div className="pay_confilmMessage_button">
                    <button onClick={payOneMonth}>Confirm</button>
                    <button onClick={paytogglePop}>Cancel</button>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>

      {/* <ul>
          {property.attributes.map((attribute, index) => (
            <li key={index}>
              <strong>{attribute.trait_type}</strong> : {attribute.value}
            </li>
          ))}
        </ul> */}
      {/* <div className="pay_loading_overlay">
        <div className="loading-spinner">
          <div className="loading-successmessage">aaa</div>
        </div>
      </div> */}
    </div>
  );
};

export default PayRent;
