import "./index.css";
import { useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
// Components

import Home from "./components/Home";
import Dao from "./components/kokoDao/01-kokoWorld";
import MyOrders from "./components/MyOrders";
import About from "./components/About";
function App() {
  const [account, setAccount] = useState(null);
  return (
    <div>
      <Router basename="/RentChain_Web3">
        {/* <Router> */}
        <Routes>
          <Route
            path="/"
            element={<Home account={account} setAccount={setAccount} />}
          />
          {/* 定义根路径 "/" 对应的组件为 Home */}
          <Route
            path="/Home"
            element={<Home account={account} setAccount={setAccount} />}
          />
          <Route
            path="/Dao"
            element={<Dao account={account} setAccount={setAccount} />}
          />
          <Route
            path="/About"
            element={<About account={account} setAccount={setAccount} />}
          />
          <Route
            path="/MyOrders"
            element={<MyOrders account={account} setAccount={setAccount} />}
          />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
