import React from "react";
import { ethers } from "ethers";
import logo from "../assets/RentChainLogo.png";
import { useNavigate } from "react-router-dom";

const Navigation = ({ account, setAccount }) => {
  const navigate = useNavigate();
  const homeOnclick = () => {
    navigate("/");
  };
  const daoOnclick = () => {
    console.log("nav-dao");
    navigate("/Dao");
  };
  // const aboutOnclick = () => {
  //   navigate("/About");
  // };
  const myOrdersOnclick = () => {
    navigate("/MyOrders");
  };
  const connectOnclick = async () => {
    // 检查窗口对象是否包含 ethereum 对象（MetaMask 注入的对象）
    if (typeof window.ethereum !== "undefined") {
      // 请求连接 MetaMask 钱包
      const accounts = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      const account = ethers.utils.getAddress(accounts[0]);
      setAccount(account);
      console.log("Connected address:", account);
    } else {
      alert("MetaMask is not installed. Please install it to proceed.");
      console.log("MetaMask is not installed.");
    }
  };

  return (
    <div className="nav">
      <ul className="nav__links">
        <li>
          <button onClick={daoOnclick}>DAO</button>
        </li>
        <li>
          <button onClick={homeOnclick}>Home</button>
        </li>
        <li>
          <a
            href="https://www.hyperchain.cn/about/company"
            target="_blank"
            rel="noopener noreferrer"
          >
            About
          </a>
          {/* target="_blank":该属性会指示浏览器在新标签页中打开链接。*/}
        </li>
      </ul>

      <div className="nav__brand">
        {/* <img src={logo} alt="Logo" /> */}
        <div className="nav__logo"> </div>
        <h1>RentChain</h1> {/* 更改为您的项目名称 */}
      </div>
      <div className="nav_address">
        {account ? (
          <div className="nav__account">
            <button
              type="button"
              className="nav__connect"
              onClick={myOrdersOnclick}
            >
              {account.slice(0, 6) + "..." + account.slice(38, 42)}
            </button>
          </div>
        ) : (
          <button
            type="button"
            className="nav__connect"
            onClick={connectOnclick}
          >
            Connect Wallet
          </button>
        )}
        {/* <button onClick={myOrdersOnclick}>myOrders</button> */}
      </div>
    </div>
  );
};

export default Navigation;
