import { ethers } from "ethers";
import { useEffect, useState } from "react";
import "./AddProperty.css";
import {
  PRIVATE_KEY0,
  PRIVATE_KEY1,
  PRIVATE_KEY2,
  HARDHAT_RPC_URL,
  SEPOLIA_PRIVATE_KEY0,
  SEPOLIA_PRIVATE_KEY1,
  SEPOLIA_PRIVATE_KEY2,
  SEPOLIA_RPC_URL,
} from "../components/accountSetting";
const AddProperty = ({ addtogglePop, loadBlockchainData, tokenId }) => {
  const [loading, setLoading] = useState(false); // 加载状态
  const [error, setError] = useState(null); // 错误状态

  async function addOnclick() {
    await addPro();
  }
  async function addPro() {
    if (typeof window.ethereum !== "undefined") {
      try {
        setLoading(true); // 开始处理时显示弹窗

        // 获取当前连接的账户
        const accounts = await window.ethereum.request({
          method: "eth_accounts",
        });

        if (accounts.length === 0) {
          // 如果没有连接账户，弹出提示
          alert("🔗 Please connect your MetaMask wallet first");
          return;
        }

        // 检查是否连接到 Sepolia 测试网
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const network = await provider.getNetwork();

        // if (network.chainId !== 11155111) {
        //   // Sepolia 测试网的 chainId 是 11155111
        //   alert("Please switch to the Sepolia testnet in MetaMask");
        //   return;
        // }

        alert("Please upload the metadata to IPFS first.");
      } catch (error) {
        console.error("Error in addproperty:", error);
        alert("Sorry, add property failed, please view console log");
      } finally {
        setLoading(false); // 完成后隐藏弹窗
      }
    } else {
      alert("MetaMask is not installed. Please install it to proceed.");
    }
  }
  // 获取详细信息
  const fetchDetails = async () => {
    try {
      setLoading(true);
      setError(null);
    } catch (error) {
      console.error("Error fetching details:", error);
      setError("Error fetching property details");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      {" "}
      <div className="addProperty_window">
        <div className="addProperty_loading-spinner">
          {loading ? (
            <div className="loading_overlay">
              <div className="loading_spinner">Loading, please wait...</div>
            </div>
          ) : (
            <div className="addProperty_container">
              <h2 className="addProperty_title">Add Your Rental Property</h2>

              <div className="addProperty_input_module">
                {/* Name Input */}
                <label className="input_name">
                  <p>Property Name:</p>
                  <input
                    type="text"
                    className="addProperty_input_Field"
                    placeholder="Enter property name"
                  />
                </label>

                {/* Address Input */}
                <label className="input_address">
                  <p>Property Address:</p>
                  <input
                    type="text"
                    className="addProperty_input_Field"
                    placeholder="Enter property address"
                  />
                </label>

                {/* Description Input */}
                <label className="input_description">
                  <p>Description:</p>
                  <textarea
                    className="addProperty_input_textarea"
                    placeholder="Enter property description"
                  />
                </label>

                {/* Image URL Input */}
                <label className="input_imageUrl">
                  <p>Image URL:</p>
                  <input
                    type="text"
                    className="addProperty_input_Field"
                    placeholder="Enter property image URL"
                  />
                </label>

                {/* Attributes Input */}
                <label className="input_attributes">
                  <p>Attributes:</p>
                  <textarea
                    className="addProperty_input_textarea"
                    placeholder="Enter property attributes"
                  />
                </label>
              </div>

              <div className="addProperty_button">
                <button onClick={addOnclick}>Commit</button>
                <button onClick={addtogglePop}>Cancel</button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AddProperty;
